"use client";
import { useState, useEffect, useRef } from "react";
import * as signalR from "@microsoft/signalr";
import { motion } from "framer-motion";
import Background from "@/components/effects/Background";

export default function ChatPage() {
  const [messages, setMessages] = useState<
    { text: string; sender: "user" | "bot" }[]
  >([]);
  const [input, setInput] = useState("");
  const messagesEndRef = useRef<HTMLDivElement | null>(null);
  const [connection, setConnection] = useState<signalR.HubConnection | null>(
    null
  );

  // Setup SignalR Connection
  useEffect(() => {
    const newConnection = new signalR.HubConnectionBuilder()
      .withUrl("http://localhost:5000/chatHub") // ðŸ”¥ Your SignalR URL
      .withAutomaticReconnect()
      .build();

    newConnection
      .start()
      .then(() => console.log("Connected to SignalR"))
      .catch((err) => console.error("SignalR Connection Error:", err));

    newConnection.on("ReceiveMessage", (user, message) => {
      setMessages((prev) => [
        ...prev,
        { text: `${user}: ${message}`, sender: "bot" },
      ]);
    });

    setConnection(newConnection);

    return () => {
      newConnection.stop();
    };
  }, []);

  // Auto-scroll to latest message
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSendMessage = async () => {
    if (!input.trim() || !connection) return;

    const user = "User"; // You can replace this with the actual username

    // Add user message
    setMessages((prev) => [...prev, { text: input, sender: "user" }]);
    setInput("");

    try {
      await connection.invoke("SendMessage", user, input);
    } catch (err) {
      console.error("Error sending message:", err);
    }
  };

  return (
    <div className="w-screen h-screen flex flex-col bg-slate-900 text-white">
      {/* Background Effect */}
      <Background />

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.map((msg, index) => (
          <motion.div
            key={index}
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3 }}
            className={`max-w-lg p-3 rounded-lg ${
              msg.sender === "user" ? "bg-blue-500 ml-auto" : "bg-gray-700"
            }`}
          >
            {msg.text}
          </motion.div>
        ))}
        <div ref={messagesEndRef} />
      </div>

      {/* Input Bar */}
      <div className="p-4 border-t border-gray-700 bg-slate-800">
        <div className="flex items-center gap-2">
          <input
            type="text"
            className="flex-1 p-2 bg-gray-900 text-white rounded-lg outline-none border border-gray-600 focus:border-blue-400"
            placeholder="Type a message..."
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && handleSendMessage()}
          />
          <button
            onClick={handleSendMessage}
            className="px-4 py-2 bg-blue-600 rounded-lg hover:bg-blue-500 transition"
          >
            Send
          </button>
        </div>
      </div>
    </div>
  );
}
